import React from "react";

export default class ContactMe extends React.Component {
    constructor(props) {
        super(props);
        this.submitForm = this.submitForm.bind(this);
        this.state = {
            status: ""
        };
    }

    render() {
        const { status } = this.state;
        return (
            <section className="colorlib-blog mb-4 contact-form" data-section="contactme">
                <div className="colorlib-narrow-content">
                    <span className="heading-meta">Contact</span>
                    <h2 className="colorlib-heading">Contact Me</h2>
                    <p class="w-responsive mx-auto mb-5">Do you have any questions or suggestions? Please feel free to contact me.</p>
                    <div class="row">
                        <div class="col-md-9 mb-md-0 mb-5">
                            <form
                                onSubmit={this.submitForm}
                                action="https://formspree.io/mrglpqbg"
                                method="POST"
                                id="contact-form"
                                name="contact-form"
                                class="form-wrapper"
                            >
                                {status === "SUCCESS" && <p>Thanks for contacting!</p>}
                                {status === "ERROR" && <p>Enter valid information.</p>}
                                <div class="row">
                                    <div class="col-md-8">
                                        <div class="md-form mb-0">
                                            <label for="subject" class="">Name</label>
                                            <input type="text" id="name" name="name" class="form-control" />
                                        </div>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-md-8">
                                        <div class="md-form mb-0">
                                            <label for="email" class="">Your email</label>
                                            <input type="text" id="email" name="email" class="form-control" />
                                        </div>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-md-12">
                                        <div class="md-form">
                                            <label for="message">Your message</label>
                                            <textarea type="text" id="message" name="message" rows="2" class="form-control md-textarea"></textarea>
                                        </div>

                                    </div>
                                </div>
                                <button class="submit-button">Send</button>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        );
    }

    submitForm(ev) {
        ev.preventDefault();
        const form = ev.target;
        const data = new FormData(form);
        const request = new XMLHttpRequest();
        request.open(form.method, form.action);
        request.setRequestHeader("Accept", "application/json");
        request.onreadystatechange = () => {
            if (request.readyState !== XMLHttpRequest.DONE) return;
            if (request.status === 200) {
                form.reset();
                this.setState({ status: "SUCCESS" });
            } else {
                this.setState({ status: "ERROR" });
            }
        };
        request.send(data);
    }
}